import React, { useCallback, useEffect, useState } from "react";
import { Col, Image, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import Pagination from "../pagination/pagination";
import { ReactComponent as EditIcon } from "../../assets/images/icons/Pen-Icon.svg";
import { ReactComponent as DeleteIcon } from "../../assets/images/icons/Bin-Icon.svg";
import { actions as item } from "../../redux/item/itemAction";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import TableWrapper from "../../components/wrapper/table-wrapper";
import { API_URL } from "../../utils/api";
import Confirmation from "../../components/dialog/confirmation";

const ItemList = (props) => {
  const navigate = useNavigate();

  const [currentPage, setCurrentPage] = useState(1);
  const [showItems, setShowItems] = useState(10);
  const [, setID] = useState();
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);

  const [inputText, setInputText] = useState("");
  let inputHandler = (e) => {
    var lowerCase = e.target.value.toLowerCase();
    setInputText(lowerCase);
  };

  useEffect(() => {
    props.actions.getItemByCatID({ id: 1 });
  }, [props.actions]);
  const userlist = props?.item?.itemList;

  const filteredData = userlist?.filter((el) => {
    if (inputText === "") {
      return el;
    } else {
      return (
        el?.name?.toLowerCase().includes(inputText) ||
        el?.type?.toLowerCase().includes(inputText)
      );
    }
  });

  let NUM_OF_RECORDS = filteredData.length;

  const onPageChanged = useCallback(
    (event, page) => {
      event.preventDefault();
      setCurrentPage(page);
    },
    [setCurrentPage]
  );

  const currentData = userlist.slice(
    (currentPage - 1) * showItems,
    (currentPage - 1) * showItems + showItems
  );

  const handleShowItemChange = (e) => {
    setShowItems(e.target.value);
  };

  const handleItemGet = (item) => {
    navigate(`/item/update/${item.id}`);
  };

  const confirmItemDelete = (e) => {
    e.preventDefault();
    // props.actions.deleteCategory({ item: id });
    setShow(false);
  };

  const deleteItem = (item) => {
    setID(item.id);
    setShow(true);
  };

  return (
    <Row>
      <Col sm="12">
        <TableWrapper
          title="Item List"
          buttonTitle="Add Item"
          buttonAction={(e) => navigate("/item/add")}
          handleShowItemChange={handleShowItemChange}
          inputHandler={inputHandler}
        >
          <div className="table-responsive">
            <table
              id="user-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              <thead>
                <tr className="ligth">
                  <th>Profile</th>
                  <th>Name</th>
                  <th>Price</th>
                  <th>Type</th>
                  <th>Status</th>
                  <th min-width="100px">Action</th>
                </tr>
              </thead>
              <tbody>
                {currentData?.map((item, idx) => (
                  <tr key={idx}>
                    <td className="text-center">
                      <Image
                        className="bg-soft-primary rounded img-fluid avatar-40 me-3"
                        src={`${API_URL}/static/items/${item.images[0]}`}
                        alt="profile"
                      />
                    </td>
                    <td>{item?.name}</td>
                    <td>{item?.price}</td>
                    <td>{item?.type}</td>
                    <td className="text-center">
                      <div className="maindiv">
                        <div className="col-md-6">
                          {item.status === "Active" ? (
                            <span
                              className="badge"
                              style={{
                                backgroundColor: "green",
                              }}
                            >
                              {item.status}
                            </span>
                          ) : (
                            <span
                              className="badge"
                              style={{
                                backgroundColor: "red",
                              }}
                            >
                              {item.status}
                            </span>
                          )}
                        </div>
                      </div>
                    </td>
                    <td>
                      <div className="flex align-items-center list-user-action">
                        <button
                          className="btn btn-sm btn-icon btn-warning mx-1"
                          data-toggle="tooltip"
                          data-placement="top"
                          title=""
                          data-original-title="Edit"
                          onClick={() => handleItemGet(item)}
                        >
                          <span className="btn-inner">
                            <EditIcon />
                          </span>
                        </button>

                        <button
                          className="btn btn-sm btn-icon btn-danger mx-1"
                          data-toggle="tooltip"
                          data-placement="top"
                          onClick={() => deleteItem(item)}
                        >
                          <DeleteIcon />
                        </button>
                      </div>
                      <Confirmation
                        show={show}
                        handleClose={handleClose}
                        description="Are you sure you want to delete ?"
                        handleClick={confirmItemDelete}
                        yesText="Yes"
                        noText="No"
                      />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </TableWrapper>
      </Col>

      <span className="col-md-6">
        Showing {(currentPage - 1) * showItems + 1} to{" "}
        {(currentPage - 1) * showItems + showItems > NUM_OF_RECORDS
          ? NUM_OF_RECORDS
          : showItems}{" "}
        of {NUM_OF_RECORDS} entries
      </span>

      <Pagination
        totalRecords={NUM_OF_RECORDS}
        pageLimit={showItems}
        onPageChanged={onPageChanged}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(item, dispatch) },
});

const mapStateToProps = (state) => ({
  item: state.item,
});

export default connect(mapStateToProps, mapDispatchToProps)(ItemList);
