import React, { useState, useEffect } from "react";
import { Row, Col, Button } from "react-bootstrap";
import { useNavigate, useParams } from "react-router-dom";
import { Form, FormikProvider, useFormik } from "formik";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import * as Yup from "yup";

import { actions as discount } from "../../redux/discount/discountAction";
import FormikController from "../../components/controls/FormikController";
import { discountList } from "../../shared/contant";
import Card from "../../components/card";

const ManageDiscount = (props) => {
  const initialValues = {
    type: "",
    is_active: "",
  };

  const navigate = useNavigate();

  const params = useParams();

  const [initFormData, setInitFormData] = useState(initialValues);
  const [discount] = useState(
    Object.keys(discountList).map((key) => ({
      label: key,
      value: discountList[key],
    }))
  );

  useEffect(() => {
    if (params?.id) {
      props.actions.getDiscountTypeById(params);
    }
  }, [params, props.actions]);

  const Data = props?.discount?.discountItem;

  useEffect(() => {
    if (params?.id) {
      setInitFormData(Data);
    }
  }, [params?.id, Data]);

  const onSubmit = (values) => {
    const data = {
      type: values.type,
      is_active: values.is_active,
    };

    if (!params?.id && props?.mode === "Add") {
      props.actions.addDiscountType({
        body: data,
        callback: () => navigate("/discount/list"),
      });
    } else {
      props.actions.updateDiscountType({
        body: { data, params: params.id },
        callback: () => navigate("/discount/list"),
      });
    }
  };

  const validationSchema = Yup.object({
    type: Yup.string().required("Required"),
    is_active: Yup.string(),
  });

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values } = formik;

  return (
    <Row>
      <Col xl="10" lg="9">
        <Card>
          <Card.Header className="d-flex justify-content-between">
            <h3 className="header-title">
              {props?.mode === "Add" ? "Add" : "Update"} Discount
            </h3>
          </Card.Header>
          <Card.Body>
            <FormikProvider value={formik}>
              <Form onSubmit={handleSubmit}>
                <div className="row">
                  <div className="col-sm-12 form-group">
                    <FormikController
                      control="select"
                      label="Type"
                      name="type"
                      onChange={handleChange}
                      className="selectpicker form-control"
                      options={discount}
                      value={values.type}
                    />
                  </div>
                </div>

                {!params.id && props?.mode === "Add" ? (
                  <div className="row">
                    <div className="col-md-6">
                      <FormikController
                        control="checkbox"
                        name="is_active"
                        option="is_active"
                        label="Is Active ?"
                        className="me-2 form-check-input"
                        value={values?.is_active}
                        onChange={handleChange}
                      />
                    </div>
                  </div>
                ) : null}

                {props?.discount?.error ? (
                  <span
                    style={{
                      color: "Red",
                      fontSize: "smaller",
                    }}
                  >
                    {props?.discount?.error}
                  </span>
                ) : null}

                <Button
                  type="submit"
                  variant="btn btn-primary"
                  disabled={props?.discount?.loading}
                >
                  {props.mode === "Add" ? "Add" : "Update"} Discount
                </Button>
              </Form>
            </FormikProvider>
          </Card.Body>
        </Card>
      </Col>
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(discount, dispatch) },
});

const mapStateToProps = (state) => ({
  discount: state.discount,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageDiscount);
