import { Form, FormikProvider, useFormik } from "formik";
import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "react-bootstrap";
import { connect } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { bindActionCreators } from "redux";
import * as Yup from "yup";

import FormikController from "../../components/controls/FormikController";
import { actions as role } from "../../redux/roles/roles-action";
import { roleList } from "../../shared/contant";
import Card from "../../components/card";

const initialState = {
  name: "",
  role_type: "",
  status: "",
};

const ManageRole = (props) => {
  const navigate = useNavigate();
  const params = useParams();

  const validationSchema = Yup.object({
    name: Yup.string().required().label("Role Name"),
  });

  useEffect(() => {
    props.actions.getRoleById({ id: params.id });
  }, [params.id, props.actions]);

  const [initFormData, setInitFormData] = useState(initialState);
  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const [roleType, setRoleType] = useState("");
  const [rolesList, setRolesList] = useState([]);

  useEffect(() => {
    setRolesList(
      Object.keys(roleList).map((key) => ({
        value: key,
        label: roleList[key],
      }))
    );
  }, []);

  const { handleChange, handleSubmit, values } = formik;

  const role = props?.role?.role;

  useEffect(() => {
    setRoleType(role?.role_type);
  }, [props?.role, role?.role_type]);

  useEffect(() => {
    if (params?.id && role?.id) {
      const roleData = {
        name: role?.name,
        status: role?.status === "Active" ? true : false,
      };
      setInitFormData(roleData);
    }
  }, [params?.id, role]);

  const onSubmit = (values) => {
    const roleData = {
      name: values.name,
      role_type: roleType,
    };

    if (props?.mode === "Add") {
      props.actions.addRole({
        body: roleData,
        callback: () => navigate("/role/list"),
      });
    } else {
      values.status
        ? (roleData.status = "Active")
        : (roleData.status = "Inactive");

      props.actions.updateRole({
        body: roleData,
        id: params.id,
        callback: () => navigate("/role/list"),
      });
    }
  };

  return (
    <Row>
      <Col xl="12" lg="9">
        <Card
          className="mb-0 iq-content rounded-bottom"
          style={{ marginTop: "-103px" }}
        >
          <Card.Header className="d-flex justify-content-between">
            <h3 className="header-title">
              {props.mode === "Add" ? "Add" : "Update"} Role
            </h3>
          </Card.Header>
          <Card.Body>
            <FormikProvider value={formik}>
              <Form onSubmit={handleSubmit}>
                <div className="row">
                  <FormikController
                    control="input"
                    type="text"
                    name="name"
                    classname="form-control"
                    label="Role Name"
                    onChange={handleChange}
                  />
                </div>

                <div className="row">
                  <FormikController
                    label="Category"
                    control="select"
                    name="role_type"
                    onChange={(e) => setRoleType(e.target.value)}
                    className="selectpicker form-control"
                    options={rolesList}
                    value={roleType}
                  />
                </div>

                {props?.mode === "Update" ? (
                  <div className="row">
                    <div className="col-md-6">
                      <FormikController
                        control="checkbox"
                        name="status"
                        option="status"
                        label="Is active ?"
                        className="me-2 form-check-input"
                        value={values.status}
                        onChange={handleChange}
                        //   isControl={false}
                      />
                    </div>
                  </div>
                ) : null}

                {props?.role?.error ? (
                  <span
                    style={{
                      color: "Red",
                      fontSize: "smaller",
                    }}
                  >
                    {props?.role?.error}
                  </span>
                ) : null}

                <div className="d-flex justify-content-start">
                  <Button
                    type="submit"
                    variant="btn btn-primary"
                    disabled={props?.role?.loading}
                  >
                    {props.mode === "Add" ? "Add" : "Update"} Role
                  </Button>
                </div>
              </Form>
            </FormikProvider>
          </Card.Body>
        </Card>
      </Col>
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(role, dispatch) },
});

const mapStateToProps = (state) => ({
  role: state.role,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageRole);
