import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "react-bootstrap";
import Card from "../../components/card";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { actions as category } from "../../redux/category/categoryAction";
import { actions as item } from "../../redux/item/itemAction";
import { Form, FormikProvider, useFormik } from "formik";
import * as Yup from "yup";
import { useParams } from "react-router-dom";
import FormikController from "../../components/controls/FormikController";

const ManageItem = (props) => {
  const params = useParams();

  useEffect(() => {
    props.actions.getItemByID({ id: params.id });
  }, [params?.id, props.actions]);

  const initialState = {
    name: "",
    description: "",
    price: "",
    discount: "",
    type: "",
    is_available: "",
    files: [],
    category_id: "",
  };

  const validationSchema = Yup.object().shape({
    name: Yup.string().required().label("Product Name"),
    description: Yup.string().required().label("Description"),
    price: Yup.number().required().label("Price"),
    discount: Yup.number().required().label("Discount"),
    type: Yup.string().required("Required"),
    // files: Yup.string(),
  });

  const [initFormData] = useState(initialState);
  const [images, setImages] = useState([]);

  const handleChangeImages = (e) => {
    const files = [...e.target.files];
    if (images?.length + files?.length > 5) {
      alert("Only 5 files accepted.");
    } else {
      let err = "";
      let newImages = [];
      let maxLimit = false;

      files.forEach((file) => {
        if (!file) {
          alert("File does not exist.");
        }
        if (file.type === "video/mp4") {
          if (file.size > 1024 * 1024 * 5) {
            maxLimit = true;
            alert("Video size must be less than 5 mb.");
          }
        }
        if (!maxLimit) newImages.push(file);
      });
      if (err) {
      }
      setImages([...images, ...newImages]);
    }
  };

  useEffect(() => {
    props.actions.getCategory();
  }, [props.actions]);

  const onSubmit = (values) => {
    const formData = new FormData();

    formData.append("name", values.name);
    formData.append("description", values.description);
    formData.append("price", values.price);
    formData.append("discount", values.discount);
    formData.append(
      "files",
      images.map((image) => values.files.push(image))
    );
    formData.append("is_available", values.is_available);
    formData.append("type", values.type);
    formData.append("category_id", values.category_id);

    props.actions.ManageItem({ body: formData });
  };

  const categoryList = props?.category?.category;

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values } = formik;

  const filteredCategory = categoryList
    ?.filter(({ cname }) => cname !== null)
    .map(({ cid, cname }) => {
      return cname !== null ? { value: cid, label: cname } : null;
    });

  return (
    <Row>
      <Col xl="12" lg="9">
        <Card
          className="mb-0 iq-content rounded-bottom"
          style={{ marginTop: "-103px" }}
        >
          <Card.Header className="d-flex justify-content-between">
            <h3 className="header-title">
              {props?.mode === "Add" ? "Add" : "Update"} Item
            </h3>
          </Card.Header>
          <Card.Body>
            <FormikProvider value={formik}>
              <Form onSubmit={handleSubmit}>
                <div className="row">
                  <FormikController
                    control="input"
                    type="text"
                    name="name"
                    classname="form-control"
                    label="Product Name"
                    onChange={handleChange}
                  />
                </div>

                <div className="row">
                  <FormikController
                    control="input"
                    type="text"
                    name="description"
                    classname="form-control"
                    label="Description"
                    onChange={handleChange}
                  />
                </div>

                <div className="row">
                  <FormikController
                    control="input"
                    type="text"
                    name="price"
                    classname="form-control"
                    label="Price"
                    onChange={handleChange}
                  />
                </div>

                <div className="row">
                  <FormikController
                    control="input"
                    type="text"
                    name="discount"
                    classname="form-control"
                    label="Discount"
                    onChange={handleChange}
                  />
                </div>

                <div className="row">
                  <div className="col-sm-12">
                    <div className="form-group">
                      <input
                        onChange={handleChangeImages}
                        type="file"
                        name="image"
                        id="image"
                        className="form-control"
                        multiple
                        accept="image/*,video/*"
                      />
                    </div>
                  </div>
                </div>

                <div className="row">
                  <FormikController
                    control="radio"
                    type="radio"
                    name="type"
                    classname="form-control"
                    label="Type"
                    onChange={handleChange}
                    options={[
                      { value: "Veg", label: "Veg" },
                      { value: "Non-veg", label: "Non-veg" },
                    ]}
                  />
                </div>

                <div className="row">
                  <FormikController
                    label="Category"
                    control="select"
                    name="category_id"
                    onChange={handleChange}
                    className="selectpicker form-control"
                    options={filteredCategory}
                  />
                </div>

                <div className="row">
                  <FormikController
                    control="checkbox"
                    name="is_available"
                    option="is_available"
                    label="Is available ?"
                    className="me-2 form-check-input"
                    value={values.is_available}
                    onChange={handleChange}
                  />
                </div>

                {props?.auth?.error ? (
                  <span
                    style={{
                      color: "Red",
                      fontSize: "smaller",
                    }}
                  >
                    {props?.auth?.error}
                  </span>
                ) : null}

                <div className="d-flex justify-content-start">
                  <Button
                    type="submit"
                    disabled={props?.item?.loading}
                    variant="btn btn-primary"
                  >
                    Add Product
                  </Button>
                </div>
              </Form>
            </FormikProvider>
          </Card.Body>
        </Card>
      </Col>
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators({ ...category, ...item }, dispatch) },
});

const mapStateToProps = (state) => ({
  category: state.category,
  item: state.item,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageItem);
