import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { useNavigate, useParams } from "react-router-dom";
import { Row, Col, Button } from "react-bootstrap";
import * as Yup from "yup";
import { Form, FormikProvider, useFormik } from "formik";

import Card from "../../components/card";
import { actions as category } from "../../redux/category/categoryAction";
import FormikController from "../../components/controls/FormikController";

const initialValues = {
  category_name: "",
  file: [],
  is_active: "",
  is_feature: "",
};

function ManageCategory(props) {
  const navigate = useNavigate();

  const categoryData = props?.category?.categoryItem;
  const categoryList = props?.category?.category;

  const catID = categoryList?.map((list) => list.gpid);

  const uniqueCategory = categoryList?.filter(
    ({ gpid }, index) => !catID.includes(gpid, index + 1)
  );

  const params = useParams();

  const [initFormData, setInitFormData] = useState(initialValues);
  const [isSubCat, setIsSubCat] = useState(false);
  const [isChildCat, setIsChildCat] = useState(false);
  const [file, setFile] = useState({});

  const validationSchema = Yup.object({
    category_name: Yup.string().required("Required"),
    file: Yup.array(),
    is_active: Yup.string(),
    is_feature: Yup.string(),
  });

  useEffect(() => {
    if (params.id) {
      props.actions.getCategoryById(params);
    } else {
      props.actions.getCategory();
    }
  }, [props.actions, params]);

  useEffect(() => {
    if (params?.id && categoryData?.id) {
      const category = {
        file: [categoryData?.image],
        category_name: categoryData?.name,
        is_active: categoryData?.status === "Active" ? true : false,
        is_feature: categoryData?.feature,
      };
      setInitFormData(category);
      setFile(category?.image);
    }
  }, [params?.id, categoryData]);

  const onSubmit = (data) => {
    const formData = new FormData();

    formData.append("category_name", data?.category_name);
    formData.append("file", file);
    formData.append("is_active", data.is_active);
    formData.append("is_feature", data.is_feature);

    if (!params?.id && props?.mode === "Add") {
      if (data.parent_id) {
        formData.append("parent_id", data.parent_id);
      }

      props.actions.addCategory({
        body: formData,
        callback: () => navigate("/category/list"),
      });
    } else {
      formData.append("parent_id", categoryData?.parent_id);
      props.actions.updateCategory({
        body: {
          formData,
          params: params.id,
        },
        callback: () => navigate("/category/list"),
      });
    }
  };

  const handleChangeImages = (e) => {
    const file = e.target.files[0];
    setFile(file);
  };

  const handleIsSubCat = () => {
    setIsSubCat(!isSubCat);
  };

  const handleIsChildCat = () => {
    setIsChildCat(!isChildCat);
  };

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values } = formik;

  return (
    <>
      <Row>
        <Col xl="12" lg="9">
          <Card
            className="mb-0 iq-content rounded-bottom"
            style={{ marginTop: "-103px" }}
          >
            <Card.Header className="d-flex justify-content-between">
              <h3 className="header-title">
                {props?.mode === "Add" ? "Add" : "Update"} Category
              </h3>
            </Card.Header>
            <Card.Body>
              <FormikProvider value={formik}>
                <Form onSubmit={handleSubmit}>
                  <div className="row">
                    <div className="col-sm-12">
                      <FormikController
                        control="input"
                        type="text"
                        name="category_name"
                        className="form-control"
                        label="Category Name"
                        onChange={handleChange}
                      />
                    </div>
                  </div>

                  {!params.id && props?.mode === "Add" && !isChildCat ? (
                    <div className="row">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <input
                            onChange={handleChangeImages}
                            type="file"
                            name="file"
                            id="file"
                            src={file}
                            className="form-control"
                            accept="image/*,video/*"
                          />
                        </div>
                      </div>
                    </div>
                  ) : null}

                  {!params.id && !isChildCat ? (
                    <div className="row">
                      <div className="col-md-6">
                        <FormikController
                          control="checkbox"
                          name="is_sub_active"
                          option="is_sub_active"
                          label="Is sub-category ?"
                          className="me-2 form-check-input"
                          value={isSubCat}
                          onChange={handleIsSubCat}
                          isControl={false}
                        />
                      </div>
                    </div>
                  ) : null}

                  {!params.id && props?.mode === "Add" && isSubCat ? (
                    <div className="row">
                      <div className="col-sm-12">
                        <FormikController
                          control="select"
                          label="Category"
                          name="parent_id"
                          onChange={handleChange}
                          className="selectpicker form-control"
                          options={uniqueCategory?.map(({ gpid, gpname }) => ({
                            value: gpid,
                            label: gpname,
                          }))}
                        />
                      </div>
                    </div>
                  ) : null}

                  {!params.id && props?.mode === "Add" && !isSubCat ? (
                    <div className="row">
                      <div className="col-md-6">
                        <FormikController
                          control="checkbox"
                          name="is_child_active"
                          option="is_child_active"
                          label="Is child-category ?"
                          className="me-2 form-check-input"
                          value={isChildCat}
                          onChange={handleIsChildCat}
                          isControl={false}
                        />
                      </div>
                    </div>
                  ) : null}

                  {!params.id && props?.mode === "Add" && isChildCat ? (
                    <div className="row">
                      <div className="col-sm-12">
                        <FormikController
                          control="select"
                          label="Category"
                          name="parent_id"
                          onChange={handleChange}
                          className="selectpicker form-control"
                          options={uniqueCategory?.map(({ pid, pname }) => ({
                            value: pid,
                            label: pname,
                          }))}
                        />
                      </div>
                    </div>
                  ) : null}

                  <div className="row">
                    <div className="col-md-6">
                      <FormikController
                        control="checkbox"
                        name="is_active"
                        option="is_active"
                        label="Is Active ?"
                        className="me-2 form-check-input"
                        value={values?.is_active}
                        onChange={handleChange}
                      />
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-md-6">
                      <FormikController
                        control="checkbox"
                        name="is_feature"
                        option="is_feature"
                        label="Is Feature ?"
                        className="me-2 form-check-input"
                        value={values?.is_feature}
                        onChange={handleChange}
                      />
                    </div>
                  </div>

                  {props?.category?.error ? (
                    <span
                      style={{
                        color: "Red",
                        fontSize: "smaller",
                      }}
                    >
                      {props?.category?.error}
                    </span>
                  ) : null}

                  <div className="d-flex justify-content-start">
                    <Button
                      type="submit"
                      variant="primary"
                      disabled={props?.category?.loading}
                    >
                      {props?.mode === "Add" ? "Add" : "Update"} Category
                    </Button>
                  </div>
                </Form>
              </FormikProvider>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </>
  );
}

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(category, dispatch) },
});

const mapStateToProps = (state) => ({
  category: state.category,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageCategory);
