import React, { useEffect } from "react";
import { Row, Col } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { useParams } from "react-router-dom";

import { actions as order } from "../../../redux/order/orderAction";
import "../../../assets/scss/custom/Table.scss";
import Card from "../../../components/card";

const OrderD = (props) => {
  const params = useParams();

  useEffect(() => {
    if (params?.id) {
      props.actions.getOrderById({ body: params.id });
    }
  }, [params, props.actions]);

  const orderDetails = props?.order;

  return (
    <Row style={{ display: "flex", justifyContent: "space-around" }}>
      <Col sm="6">
        {props?.order?.error ? (
          <span
            style={{
              color: "Red",
              fontSize: "smaller",
            }}
          >
            {props?.order?.error}
          </span>
        ) : null}

        <Card>
          <Card.Header>
            <Card.Header.Title>
              <h4 className="card-title"> Order Details</h4>
            </Card.Header.Title>
          </Card.Header>

          <Card.Body className="px-0">
            <table style={{ width: "100px" }}>
              <tbody>
                <tr>
                  <th>UserID:</th>
                  <td>{orderDetails?.orderItem[0]?.user_id}</td>
                </tr>
                <tr>
                  <th>Total Amount:</th>
                  <td>{orderDetails?.orderItem[0]?.total_amount}</td>
                </tr>
                <tr>
                  <th>Total Paid Amount:</th>
                  <td>{orderDetails?.orderItem[0]?.total_paid_amount}</td>
                </tr>
                <tr>
                  <th>Total Save Amount:</th>
                  <td>{orderDetails?.orderItem[0]?.total_save_amount}</td>
                </tr>
                <tr>
                  <th> Payment Status:</th>
                  <td>{orderDetails?.orderItem[0]?.payment_status}</td>
                </tr>
                <tr>
                  <th> Order Status:</th>
                  <td>{orderDetails?.orderItem[0]?.order_status}</td>
                </tr>
                <tr>
                  <th> Order Date:</th>
                  <td>
                    {new Date(
                      orderDetails?.orderItem[0]?.order_date
                    ).toLocaleDateString()}
                  </td>
                </tr>
              </tbody>
            </table>
          </Card.Body>
        </Card>
      </Col>

      <Col sm="6">
        <Card>
          <Card.Header>
            <Card.Header.Title>
              <h4 className="card-title"> User Details</h4>
            </Card.Header.Title>
          </Card.Header>

          <Card.Body className="px-0">
            <table style={{ width: "100px" }}>
              <tbody>
                <tr>
                  <th> First_name :</th>
                  <td>{orderDetails?.orderItem[0]?.User?.first_name}</td>
                </tr>
                <tr>
                  <th> Middle_name:</th>
                  <td>{orderDetails?.orderItem[0]?.User?.middle_name}</td>
                </tr>
                <tr>
                  <th> Last_name:</th>
                  <td>{orderDetails?.orderItem[0]?.User?.last_name}</td>
                </tr>
                <tr>
                  <th> email:</th>
                  <td>{orderDetails?.orderItem[0]?.User?.email}</td>
                </tr>
                <tr>
                  <th> UserName:</th>
                  <td>{orderDetails?.orderItem[0]?.User?.username}</td>
                </tr>
                <tr>
                  <th> Role_Id:</th>
                  <td>{orderDetails?.orderItem[0]?.User?.role_id}</td>
                </tr>
                <tr>
                  <th> Address_Id:</th>
                  <td>{orderDetails?.orderItem[0]?.User?.address_id}</td>
                </tr>
                <tr>
                  <th> Gender:</th>
                  <td>{orderDetails?.orderItem[0]?.User?.gender}</td>
                </tr>
                <tr>
                  <th> BirthDate:</th>
                  <td>
                    {new Date(
                      orderDetails?.orderItem[0]?.User?.birthdate
                    ).toLocaleDateString()}
                  </td>
                </tr>
                <tr>
                  <th> Status:</th>
                  <td>{orderDetails?.orderItem[0]?.User?.status}</td>
                </tr>
              </tbody>
            </table>
          </Card.Body>
        </Card>
      </Col>
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(order, dispatch) },
});

const mapStateToProps = (state) => ({
  order: state.order,
});

export default connect(mapStateToProps, mapDispatchToProps)(OrderD);
