import React, { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Row, Col, Button } from "react-bootstrap";
import * as Yup from "yup";
import { Form, FormikProvider, useFormik } from "formik";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import { actions as banner } from "../../redux/banner/bannerAction";
import Card from "../../components/card";
import FormikController from "../../components/controls/FormikController";

function ManageBanner(props) {
  const params = useParams();

  const navigate = useNavigate();

  const initialState = {
    title: "",
    terms: "",
    is_active: false,
  };

  const [initFormData, setInitFormData] = useState(initialState);

  const validationSchema = Yup.object({
    title: Yup.string().required("Required"),
    terms: Yup.string(),
  });

  const [, setImages] = useState([]);
  const [productDetails, setProductDetails] = useState(initialState);

  const handleChangeImages = (e) => {
    const file = e.target.files[0];
    setImages(file);
    setProductDetails({ ...productDetails, file: file });
  };

  const Data = props.banner.bannerItem;

  useEffect(() => {
    if (params?.id && Data?.id) {
      const productInfo = {
        title: Data?.title,
        file: Data?.image,
        is_active: Data?.status === "Active" ? true : false,
        terms: Data?.terms,
      };
      setInitFormData(productInfo);
    }
  }, [params?.id, Data]);

  const onSubmit = (data) => {
    var formData = new FormData();
    formData.append("title", data?.title);
    formData.append("file", productDetails?.file);
    formData.append("is_active", data.is_active);
    formData.append("terms", data?.terms);

    if (!params?.id && props?.mode === "Add") {
      props.actions.addBanner({
        body: formData,
        callback: () => navigate("/banner/list"),
      });
    } else {
      props.actions.updateBanner({
        body: { formData, params: params.id },
        callback: () => navigate("/banner/list"),
      });
    }
  };

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values } = formik;

  return (
    <Row>
      <Col xl="10" lg="9">
        <Card
          className="mb-0 iq-content rounded-bottom"
          style={{ marginTop: "-103px" }}
        >
          <Card.Header className="d-flex justify-content-between">
            <h3 className="header-title">
              {props?.mode === "Add" ? "Add" : "Update"} Banner
            </h3>
          </Card.Header>

          <Card.Body>
            <FormikProvider value={formik}>
              <Form onSubmit={handleSubmit}>
                <div className="row">
                  <div className="col-sm-12">
                    <FormikController
                      control="input"
                      type="text"
                      name="title"
                      className="form-control"
                      label="Title"
                      onChange={handleChange}
                    />
                  </div>
                </div>

                <div className="row">
                  <div className="col-sm-12">
                    <div className="form-group">
                      <input
                        onChange={handleChangeImages}
                        type="file"
                        name="file"
                        id="file"
                        src={productDetails.file}
                        className="form-control"
                        accept="image/*,video/*"
                      />
                    </div>
                  </div>
                </div>

                <div className="row">
                  <FormikController
                    control="textarea"
                    type="text"
                    name="terms"
                    className="form-control"
                    label="Terms & Conditions"
                    onChange={handleChange}
                  />
                </div>

                <div className="row">
                  <div className="col-md-6">
                    <FormikController
                      control="checkbox"
                      name="is_active"
                      option="is_active"
                      label="Is Active ?"
                      className="me-2 form-check-input"
                      value={values?.is_active}
                      onChange={handleChange}
                    />
                  </div>
                </div>

                {props?.banner?.error ? (
                  <span
                    style={{
                      color: "Red",
                      fontSize: "smaller",
                    }}
                  >
                    {props?.banner?.error}
                  </span>
                ) : null}

                <div className="d-flex justify-content-start">
                  <Button
                    type="submit"
                    variant="primary"
                    disabled={props?.banner?.loading}
                  >
                    {props.mode === "Add" ? "Add" : "Update"} Banner
                  </Button>
                </div>
              </Form>
            </FormikProvider>
          </Card.Body>
        </Card>
      </Col>
    </Row>
  );
}

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(banner, dispatch) },
});

const mapStateToProps = (state) => ({
  banner: state.banner,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageBanner);
