import { takeEvery, call, put } from "redux-saga/effects";
import { api, endPoints } from "../../utils/api";
import { actions } from "./discountAction";

function* addDiscountType({ payload }) {
  try {
    const response = yield call(
      api.post,
      endPoints.ADD_DISCOUNT_TYPE,
      payload.body
    );
    yield put(actions.setAddDiscountType(response));
    yield call(payload.callback);
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getDiscountType() {
  try {
    const response = yield call(api.get, endPoints.GET_DISCOUNT_TYPE);
    yield put(actions.setDiscountType(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getDiscountTypeById({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_DISCOUNT_TYPE_BY_ID, {
      urlParams: {
        id: payload.id,
      },
    });
    yield put(actions.setDiscountTypeById(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* updateDiscountType({ payload }) {
  try {
    const response = yield call(
      api.put,
      endPoints.UPDATE_DISCOUNT_TYPE,
      payload.body.data,
      { urlParams: { id: payload.body.params } }
    );
    yield put(actions.setUpdateDiscountType(response));
    yield call(payload.callback);
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getDiscountItemData({ payload }) {
  try {
    yield put(actions.setDishcountItemData(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* deleteDiscountType({ payload }) {
  try {
    yield call(api.delete, endPoints.DELETE_DISCOUNT_TYPE, {
      urlParams: { id: payload },
    });
    yield put(actions.setDeleteDiscountType(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

export function* discountSaga() {
  yield takeEvery(actions.addDiscountType, addDiscountType);
  yield takeEvery(actions.getDiscountType, getDiscountType);
  yield takeEvery(actions.updateDiscountType, updateDiscountType);
  yield takeEvery(actions.getDiscountItemData, getDiscountItemData);
  yield takeEvery(actions.getDiscountTypeById, getDiscountTypeById);
  yield takeEvery(actions.deleteDiscountType, deleteDiscountType);
}
