import React, { useState, useCallback, useEffect } from "react";
import { Row, Col } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import Pagination from "../pagination/pagination";

import { ReactComponent as EditIcon } from "../../assets/images/icons/Pen-Icon.svg";
import { ReactComponent as EyeIcon } from "../../assets/images/icons/Eye-Icon.svg";
import TableWrapper from "../../components/wrapper/table-wrapper";
import { actions as order } from "../../redux/order/orderAction";

const OrderList = (props) => {
  const initialState = {
    user_id: "",
    total_amount: "",
    total_paid_amount: "",
    total_save_amount: "",
    payment_status: "",
    order_status: "",
    order_date: "",
  };

  const [, setState] = useState({ initialState });
  const params = useParams();

  const [showItems, setShowItems] = useState(10);
  const handleShowItemChange = (e) => {
    setShowItems(e.target.value);
  };

  const [currentPage, setCurrentPage] = useState(1);
  let userlist = props?.order?.order;
  let NUM_OF_RECORDS = userlist?.length;

  const navigate = useNavigate();

  const onPageChanged = useCallback(
    (event, page) => {
      event.preventDefault();
      setCurrentPage(page);
    },
    [setCurrentPage]
  );

  const handleClick = (item) => (e) => {
    e.preventDefault();

    const data = {
      user_id: item?.user_id,
      total_amount: item?.total_amount,
      total_paid_amount: item?.total_paid_mount,
      total_save_amount: item?.total_save_amount,
      payment_status: item?.payment_status,
      order_status: item?.order_status,
      order_date: item?.order_date,
    };

    navigate(`/order/details/${item.id}`);

    setState(data);
  };

  const Data = props?.order?.order?.map((item) => {
    return item;
  });

  let inputHandler = (e) => {
    var lowerCase = parseInt(e.target.value);
    setInputText(lowerCase);
  };

  const [inputText, setInputText] = useState("");
  const filteredData = Data?.filter((el) => {
    const strID = el.user_id.toString();
    const strAmount = el.total_amount.toString();
    const strPaid = el.total_paid_amount.toString();
    const strSaved = el.total_save_amount.toString();

    if (!inputText) {
      return el;
    } else {
      return (
        strID.includes(inputText) ||
        strAmount.includes(inputText) ||
        strPaid.includes(inputText) ||
        strSaved.includes(inputText)
      );
    }
  });

  useEffect(() => {
    props.actions.getOrder(params);
  }, [props.actions, params]);

  const handleItemGet = (item) => (e) => {
    e.preventDefault();
    props.actions.getOrderItemData(item);
    navigate(`/order/update/${item.id}`);
  };

  return (
    <Row>
      <Col sm="12">
        <TableWrapper
          title="Order List"
          handleShowItemChange={handleShowItemChange}
          inputHandler={inputHandler}
        >
          <div className="table-responsive">
            <table
              id="user-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              {filteredData?.length === 0 ? (
                <tbody>
                  <tr
                    style={{
                      display: "flex",
                      justifyContent: "center",
                    }}
                  >
                    <td>
                      <h3>No Record Found</h3>
                    </td>
                  </tr>
                </tbody>
              ) : (
                <>
                  <thead>
                    <tr className="ligth" style={{ textAlign: "center" }}>
                      <th>UserId</th>
                      <th>Amount</th>
                      <th>Paid Amount</th>
                      <th>Saved Amount</th>
                      <th>Payment Status</th>
                      <th>Order Status</th>
                      <th>Order Date</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {filteredData?.map((item, idx) => (
                      <tr key={idx} style={{ textAlign: "center" }}>
                        <td>{item?.user_id}</td>
                        <td>{item?.total_amount}</td>
                        <td>{item?.total_paid_amount}</td>
                        <td>{item?.total_save_amount}</td>
                        <td>{item?.payment_status}</td>
                        <td>{item?.order_status}</td>
                        <td>
                          {new Date(item?.order_date).toLocaleDateString()}
                        </td>
                        <td>
                          {item?.action}
                          <div className="flex align-items-center list-user-action">
                            <button
                              className="btn btn-sm btn-icon btn-warning"
                              data-toggle="tooltip"
                              data-placement="top"
                              title=""
                              navigate="/order/order-details"
                              data-original-title="View"
                              onClick={handleClick(item)}
                            >
                              <span className="btn-inner">
                                <EyeIcon />
                              </span>
                            </button>
                            <button
                              className="btn btn-sm btn-icon btn-warning"
                              data-toggle="tooltip"
                              data-placement="top"
                              data-original-title="Edit"
                              onClick={handleItemGet(item)}
                            >
                              <EditIcon />
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))}

                    {props?.order?.error ? (
                      <span
                        style={{
                          color: "Red",
                          fontSize: "smaller",
                        }}
                      >
                        {props?.order?.error}
                      </span>
                    ) : null}
                  </tbody>
                </>
              )}
            </table>
          </div>
        </TableWrapper>
      </Col>

      <span className="col-md-6">
        Showing {(currentPage - 1) * showItems + 1} to{" "}
        {(currentPage - 1) * showItems + showItems > NUM_OF_RECORDS
          ? NUM_OF_RECORDS
          : showItems}{" "}
        of {NUM_OF_RECORDS} entries
      </span>

      <Pagination
        totalRecords={NUM_OF_RECORDS}
        pageLimit={showItems}
        onPageChanged={onPageChanged}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(order, dispatch) },
});

const mapStateToProps = (state) => ({
  order: state.order,
});

export default connect(mapStateToProps, mapDispatchToProps)(OrderList);
