import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  item: null,
  itemList: [],
};

const itemReducer = createSlice({
  name: "item",
  initialState,
  reducers: {
    addItem(state) {
      state.loading = true;
    },
    setItem(state, { payload }) {
      state.loading = false;
      state.item = payload.data;
    },
    actionFalied(state, { payload }) {
      state.loading = false;
      state.error = payload?.response?.data?.message;
    },
    getItemByCatID(state) {
      state.loading = true;
    },
    setItemByCatID(state, { payload }) {
      state.loading = false;
      state.itemList = payload;
    },
    getItemByID(state) {
      state.loading = true;
    },
    setItemByID(state, { payload }) {
      state.loading = false;
      state.item = payload;
    },
  },
});

export const { name, actions } = itemReducer;
export default itemReducer.reducer;
