import React, { useCallback, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Col, Row } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { ReactComponent as DeleteIcon } from "../../assets/images/icons/Bin-Icon.svg";
import { ReactComponent as EditIcon } from "../../assets/images/icons/Pen-Icon.svg";
import TableWrapper from "../../components/wrapper/table-wrapper";
import { actions as role } from "../../redux/roles/roles-action";
import Confirmation from "../../components/dialog/confirmation";
import Pagination from "../pagination/pagination";

const RolesList = (props) => {
  const navigate = useNavigate();

  const roleList = props?.role?.roleList;
  const [show, setShow] = useState(false);

  useEffect(() => {
    props.actions.getRoles();
  }, [props.actions]);

  const [showItems, setShowItems] = useState(10);
  const handleShowItemChange = (e) => {
    setShowItems(e.target.value);
  };

  const [inputText, setInputText] = useState("");
  let inputHandler = (e) => {
    var lowerCase = e.target.value.toLowerCase();
    setInputText(lowerCase);
  };

  const filteredData = roleList?.filter((el) => {
    if (!inputText) {
      return el;
    } else {
      return (
        el?.name?.toLowerCase().includes(inputText) ||
        el?.role_type?.toLowerCase().includes(inputText)
      );
    }
  });

  let NUM_OF_RECORDS = filteredData?.length;

  const [currentPage, setCurrentPage] = useState(1);

  const onPageChanged = useCallback(
    (event, page) => {
      event.preventDefault();
      setCurrentPage(page);
    },
    [setCurrentPage]
  );

  const currentData = filteredData?.slice(
    (currentPage - 1) * showItems,
    (currentPage - 1) * showItems + showItems
  );

  const handleRoleGet = (role) => (e) => {
    e.preventDefault();
    navigate(`/role/update/${role.id}`);
  };

  const [id, setID] = useState();
  const handleDelete = (role) => (e) => {
    e.preventDefault();
    setID(role.id);
    setShow(true);
  };

  const confirmDelete = (id) => {
    props.actions.deleteRole({ id: id });
    setShow(false);
  };

  const handleClose = () => setShow(false);

  return (
    <Row>
      <Col sm="12">
        <TableWrapper
          title="Roles List"
          buttonTitle="Add Role"
          buttonAction={() => navigate("/role/add")}
          handleShowItemChange={handleShowItemChange}
          inputHandler={inputHandler}
        >
          <div className="table-responsive">
            <table
              id="role-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              {filteredData?.length === 0 ? (
                <tbody>
                  <tr
                    style={{
                      display: "flex",
                      justifyContent: "center",
                    }}
                  >
                    <td>
                      <h3>No Record Found</h3>
                    </td>
                  </tr>
                </tbody>
              ) : (
                <>
                  <thead>
                    <tr className="light">
                      <th className="text-center">Name</th>
                      <th className="text-center">Type</th>
                      <th className="text-center">Status</th>
                      <th className="text-center" min-width="100px">
                        Actions
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentData?.map((role, idx) => (
                      <tr key={idx}>
                        <td className="text-center">
                          <div className="maindiv">
                            <div
                              className={
                                role?.status === "Active" ? "status" : ""
                              }
                            />
                            <div className="col-md-6">
                              <span className="cat-name">{role?.name}</span>
                            </div>
                          </div>
                        </td>

                        {role?.role_type ? (
                          <td className="text-center">
                            <div className="maindiv">
                              <div className="col-md-2">
                                <span className="cat-name">
                                  {role?.role_type}
                                </span>
                              </div>
                            </div>
                          </td>
                        ) : (
                          <td></td>
                        )}

                        {role?.status ? (
                          <td className="text-center">
                            <div className="maindiv">
                              <div className="col-md-12">
                                {role.status === "Active" ? (
                                  <span
                                    className="badge"
                                    style={{
                                      backgroundColor: "green",
                                    }}
                                  >
                                    {role.status}
                                  </span>
                                ) : (
                                  <span
                                    className="badge"
                                    style={{
                                      backgroundColor: "red",
                                    }}
                                  >
                                    {role.status}
                                  </span>
                                )}
                              </div>
                            </div>
                          </td>
                        ) : (
                          <td></td>
                        )}
                        <td>
                          <div className="flex align-items-center list-user-action">
                            <button
                              className="btn btn-sm btn-icon btn-warning"
                              data-toggle="tooltip"
                              data-placement="top"
                              title=""
                              data-original-title="Edit"
                              onClick={handleRoleGet(role)}
                            >
                              <span className="btn-inner">
                                <EditIcon />
                              </span>
                            </button>

                            <span
                              className="btn btn-sm btn-icon btn-danger btn-inner"
                              data-toggle="tooltip"
                              data-placement="top"
                              onClick={handleDelete(role)}
                            >
                              <DeleteIcon />
                            </span>

                            <Confirmation
                              show={show}
                              handleClose={handleClose}
                              description="Are you sure you want to delete ?"
                              handleClick={() => confirmDelete(id)}
                              yesText="Yes"
                              noText="No"
                            />
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </>
              )}

              {props?.role?.error ? (
                <span
                  style={{
                    color: "Red",
                    fontSize: "smaller",
                  }}
                >
                  {props?.role?.error}
                </span>
              ) : null}
            </table>
          </div>
        </TableWrapper>
      </Col>

      <span className="col-md-6">
        Showing {(currentPage - 1) * showItems + 1} to{" "}
        {(currentPage - 1) * showItems + showItems > NUM_OF_RECORDS
          ? NUM_OF_RECORDS
          : showItems}{" "}
        of {NUM_OF_RECORDS} entries
      </span>

      <Pagination
        totalRecords={NUM_OF_RECORDS}
        pageLimit={showItems}
        onPageChanged={onPageChanged}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(role, dispatch) },
});

const mapStateToProps = (state) => ({
  role: state.role,
});

export default connect(mapStateToProps, mapDispatchToProps)(RolesList);
